import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView, FlatList } from 'react-native'
import { Input, Icon } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { DataRefs, Actions, GetBatchEnd, CurvePredict, GetCurve_FromBatch  } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement, ContentProp, ProgressBar } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

class Dashboard extends Component {
  constructor(props){
    super(props);

    const {Name, Manager, Mail, PensList, SectionsList, BatchList, MaxCap, CurCap} = this.setData(props)
    this.state = {
      farmName: Name,
      farmManager: Manager,
      farmMail: Mail, 

      PensList: PensList,
      SectionsList: SectionsList,
      BatchList: BatchList,

      MaxCap:MaxCap, 
      CurCap:CurCap,
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var Name = "Not set"
    var Manager = "Not set"
    var Mail = "Not set"

    if (localDataStore && localDataStore.farms && localDataStore.farms[Object.keys(localDataStore.userData.farms)[0]]) {
      var Farm = localDataStore.farms[Object.keys(localDataStore.userData.farms)[0]]
      Name = Farm.name?Farm.name: Name
      Manager = Farm.name?Farm.manager: Manager
      Mail = Farm.name?Farm.email: Mail
    }

    var PensList = localDataStore.pens
    var SectionsList = localDataStore.sections
    var BatchList = localDataStore.batchs

    var MaxCap = 0;
    var CurCap = 0;
    
    if (PensList && SectionsList && BatchList) {
      var TempList = {}
      Object.keys(BatchList).filter(e => e !== '__jsogObjectId' && e !== 'update_time').map((Batch) => {
        var _batch = BatchList[Batch]
        var TempGraph = GetCurve_FromBatch(_batch, localDataStore.graphs)
        var TargetWeight = TempGraph.data[TempGraph.data.length -1]

        _batch.count = 0
        _batch.target = TargetWeight
        _batch.weight = 0
        TempList[Batch] = _batch
      })

      Object.keys(SectionsList).map((Section) => {
        var Pens = SectionsList[Section].pens
        if (Pens) {
          Object.keys(Pens).filter(e => e !== '__jsogObjectId').map((Pen) => {
            MaxCap++; // Pigs pr pen (1) ???
            
            var _Pen = PensList[Pen]
            if (_Pen && _Pen.datas && _Pen.datas.count) {
              CurCap = CurCap + _Pen.datas.count
              if (_Pen.datas.batch && BatchList && BatchList[_Pen.datas.batch]) {
                var Batch = TempList[_Pen.datas.batch]

                if (localDataStore.weighings && localDataStore.weighings[Pen]) {
                  var TempGraph = GetCurve_FromBatch(Batch, localDataStore.graphs)
                  var EndDate = GetBatchEnd(Batch, TempGraph)
                  var PenWeighings = localDataStore.weighings[Pen]
                  if (PenWeighings) {
                    Object.keys(PenWeighings).forEach((weight) => {
                      var Weight = PenWeighings[weight]
                      if (Weight.time >= Batch.time && Weight.time <= EndDate) {
                        var _Weight = Weight.weight / Weight.count
                        if (_Weight > Batch.weight) Batch.weight = Weight.weight / Weight.count;
                      }
                    })
                  }
                }

                Batch.count = Batch.count + _Pen.datas.count
                TempList[_Pen.datas.batch] = Batch
              }
            }
          })
        }
      })
      BatchList = TempList;
    }
    if (MaxCap === 0) MaxCap++;

    //TODO: Farm performance calculation (Batch)
    return { Name, Manager, Mail, PensList, SectionsList, BatchList, MaxCap, CurCap }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    if (!localDataStore.sections) localDataStore.sections = {}
    if (!localDataStore.pens) localDataStore.pens = {}

    this.props.getFarm(Object.keys(localDataStore.userData.farms)[0])
    this.props.getSections(Object.keys(localDataStore.userData.farms)[0], localDataStore.sections?localDataStore.sections.update_time:null)
    this.props.getPens(Object.keys(localDataStore.userData.farms)[0], localDataStore.pens?localDataStore.pens.update_time:null)
    this.props.getBatch(Object.keys(localDataStore.userData.farms)[0])
    this.props.getWeights(Object.keys(localDataStore.userData.farms)[0], localDataStore.weighings?localDataStore.weighings.update_time:null, localDataStore.sections)
    this.props.getCurve(Object.keys(localDataStore.userData.farms)[0])
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null) {
      NavigationService.navigate("AuthStack")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        const localDataStore = this.props.firebase.localDataStore;
        if (localDataStore.farms && localDataStore.farms[Object.keys(localDataStore.userData.farms)[0]]) {
          var Farm = localDataStore.farms[Object.keys(localDataStore.userData.farms)[0]]
          if ((this.state.farmName!==Farm.name || this.state.farmManager!==Farm.manager || this.state.farmMail!==Farm.email) ||
              localDataStore.batchs && JSON.stringify(this.state.BatchList)!==JSON.stringify(localDataStore.batchs) ||
              localDataStore.sections && JSON.stringify(this.state.SectionsList)!==JSON.stringify(localDataStore.sections) ||
              localDataStore.pens && JSON.stringify(this.state.PensList)!==JSON.stringify(localDataStore.pens) ) {
            const {Name, Manager, Mail, PensList, SectionsList, BatchList, MaxCap, CurCap} = this.setData(this.props)
            this.setState({
              farmName: Name,
              farmManager: Manager,
              farmMail: Mail,
        
              PensList: PensList,
              SectionsList: SectionsList,
              BatchList: BatchList,

              MaxCap:MaxCap, 
              CurCap:CurCap,
            })
          }
        }
      }
    }
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    var FarmProgress = 0
    var BatchList = Object.keys(this.state.BatchList).filter(e => e !== '__jsogObjectId' && e !== 'update_time' && (!this.state.BatchList[e].state || this.state.BatchList[e].state !== "Closed"))
    BatchList.forEach(element => {
      var Batch = this.state.BatchList[element]
      FarmProgress += (Batch.weight/Batch.target)*100;
    });
    FarmProgress = (FarmProgress/(100*BatchList.length))*100

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullWidth, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          
          <TitleElement Text="Farm description"/>

          <View style={[Helpers.buttonContainer, Helpers.fullWidth, {height:110,}]}>
            <ContentProp texts={["Farm name", "Manager", "E-mail"]} datas={[this.state.farmName, this.state.farmManager, this.state.farmMail ]} />
          </View>
          <View style={{height:20}}/>
          <ProgressBar text={"Farm capacity: " + this.state.CurCap + " of " + this.state.MaxCap} progress={(this.state.CurCap/this.state.MaxCap) * 100 }/>
          <ProgressBar text={"Farm performance:"} progress={FarmProgress}/>

          {this.state.BatchList?(
            <View style={[Helpers.borderContainer, Helpers.fullWidth, {borderWidth:0, marginTop:10}]}>
              <Text>Batches:</Text>
              <FlatList
                data={Object.keys(this.state.BatchList).filter(e => e !== '__jsogObjectId' && e !== 'update_time' && 
                                                              (!this.state.BatchList[e].state || this.state.BatchList[e].state !== "Closed"))}
                renderItem={({item, i}) => {
                  var Batch = this.state.BatchList[item]
                  var EndDate = Batch.time;
                  var count = Batch.count
                  if (!count) count = 0
                  var progress = (Batch.weight/Batch.target)*100;

                  var TempGraph = localDataStore.graphs?localDataStore.graphs[Batch.gcurve]:null
                  if (TempGraph) {
                    var BatchEndDate = GetBatchEnd(Batch, TempGraph)
                    EndDate = BatchEndDate;

                    var Predict = CurvePredict(localDataStore, null, Batch, TempGraph)
                    if (Predict && Predict.lastWeighing && Predict.days) {
                      if (EndDate == BatchEndDate || EndDate < Predict.lastWeighing.time) { 
                        var date = new Date(Predict.lastWeighing.time)
                        date.setDate(date.getDate() + Predict.days)
                        EndDate = date.getTime()
                        if (Predict.Addition) {
                          EndDate += Predict.Addition
                        }
                      }
                    }
                  }
                  EndDate = new Date(EndDate).getDate() + "/" + (new Date(EndDate).getMonth() +1) + "/" + new Date(EndDate).getFullYear()
                  var StartDate = new Date(Batch.time).getDate() + "/" + (new Date(Batch.time).getMonth() +1) + "/" + new Date(Batch.time).getFullYear()
                  return(
                    <ProgressBar text={item + " Startdate: " + StartDate + " EndForcast: " + EndDate + ", " + count + " pigs"} progress={progress}/>
                  )
                }}
                keyExtractor={(item, index) => index.toString()}
              />
            </View>
          ):(<></>)}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    getFarm: (farmId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.FARMS, {FarmId: farmId})),
    getSections: (farmId, DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, {FarmId: farmId, DataTime: DataTime})),
    getPens: (farmId, DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {FarmId: farmId, DataTime: DataTime})),
    getBatch: (farmId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {FarmId: farmId})),
    getCurve: (farmid) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {FarmId: farmid})),
    getWeights: (farmId, DataTime, MemSections) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.WEIGHING, {FarmId: farmId, DataTime: DataTime, MemSections: MemSections})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
//export default Dashboard;
