import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, FlatList } from 'react-native'
import { Input, Icon } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, GetCurve_FromBatch, CurvePredict, GetBatchEnd, AccumulateTime, format_Time } from '../../../Stores/Firebase/Selectors'

import { LineChart } from 'react-native-chart-kit'
import { Button, TitleElement, CustomListItem, ContentProp } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'
import CurveElement, { WeightsToGraphData } from './CurveElement'

import { FlatGrid } from 'react-native-super-grid';

class DataDisplay extends Component {
  constructor(props) {
    super(props);

    var {isClosed, isVirtual, StartDate, EndDate, Animals, TargetWeight, AverageWeight, AverageGrowth, Comments_List, Comments} = this.setData(props)
    this.state = {
      Prediction: false,

      isClosed: isClosed,
      isVirtual: isVirtual,

      StartDate: StartDate,
      EndDate: EndDate,
      Animals: Animals,
      TargetWeight: TargetWeight,
      AverageWeight: AverageWeight, 
      AverageGrowth:AverageGrowth,
      Comments_List: Comments_List,
      Comments: Comments,
    }
  }

  setData(Props) {
    const localDataStore = this.props.firebase.localDataStore //Farmid
    const CurrentData = this.props.firebase.currentData;

    var Batch = localDataStore.batchs[this.props.firebase.currentData.batchId];
    var TempGraph = GetCurve_FromBatch(Batch, localDataStore.graphs, CurrentData.curveId)
    
    var TargetWeight = TempGraph.data[TempGraph.data.length -1]

    var BatchEndDate = GetBatchEnd(Batch, TempGraph)
    var EndDate = BatchEndDate;
    var StartDate = Batch.time;

    var isVirtual = false;
    var Animals = 0
    var AverageWeight = 0;
    var AverageGrowth = 0;
    var isClosed = (Batch.state && Batch.state === "Closed")?true:false
    Object.keys(Batch.pens).reverse().filter(e => (e !== '__jsogObjectId')).forEach(element => {
      if (localDataStore && localDataStore.pens && localDataStore.pens[element] || isClosed) {
        var Pen = isClosed?Batch.pens[element]:localDataStore.pens[element]
        if (Pen.datas && Pen.datas.count || Pen.count) {
          Animals += isClosed?Pen.count:Pen.datas.count;
        }
        
        if (localDataStore.weighings && localDataStore.weighings[element]) {
          var PenWeight = 0
          var lastDate = 0
          var AvagPenGrowth = 0

          var PenWeighings = isClosed?Batch.closed.weighings[element]:localDataStore.weighings[element]
          if (PenWeighings) {
            Object.keys(PenWeighings).forEach((weight) => {
              var Weight = PenWeighings[weight]
              if (Weight.time >= Batch.time && Weight.time <= EndDate) {
                if (PenWeight !== 0) {
                  var Growth_Days = (Weight.time - lastDate) / (1000 * 3600 * 24);
                  var PenGrowth = (Weight.weight - PenWeight) / Growth_Days
                  if (AvagPenGrowth === 0) AvagPenGrowth = PenGrowth
                  else AvagPenGrowth = ((AvagPenGrowth + PenGrowth) / 2) 
                }
                PenWeight = Weight.weight / Weight.count;
                lastDate = Weight.time
              }
            })
          }

          if (!isClosed) {
            var now = new Date().getTime()
            var Predict = CurvePredict(localDataStore, element, Batch, TempGraph)
            if (Predict != null && Predict.Now && now <= EndDate && new Date(now).getDate() > new Date(lastDate).getDate()){
              if (lastDate !== 0){
                var VirData = Predict.Now
                var Growth_Days = (now - lastDate) / (1000 * 3600 * 24);
                var PenGrowth = (VirData - PenWeight) / Growth_Days
                if (AvagPenGrowth === 0) AvagPenGrowth = PenGrowth
                else AvagPenGrowth = ((AvagPenGrowth + PenGrowth) / 2)
              }
              PenWeight = Predict.Now
              isVirtual = true
            }
            if (Predict && Predict.lastWeighing && Predict.days) {
              var tempEnd = new Date(EndDate).getTime()
              var date = new Date(Predict.lastWeighing.time)
              date.setDate(date.getDate() + Predict.days)
              tempEnd = date.getTime()
              if (Predict.Addition) {
                tempEnd += Predict.Addition
              }
              if (EndDate === BatchEndDate || EndDate < tempEnd) {
                EndDate = tempEnd;
              }
            }
          }
          if (AverageWeight === 0) AverageWeight = PenWeight;
          else AverageWeight = ((AverageWeight + PenWeight) / 2)
          if (AverageGrowth === 0) AverageGrowth = AvagPenGrowth;
          else AverageGrowth = ((AverageGrowth + AvagPenGrowth) / 2)
        }
      }
    });
    AverageWeight = parseFloat(AverageWeight).toFixed(2)
    AverageGrowth = parseFloat(AverageGrowth).toFixed(2)

    var Comments_List = null
    var Comments = [];
    if (Batch && Batch.state && Batch.state === "Closed" && Batch.closed && Batch.closed.comments) {
      Comments_List = []
      Object.keys(Batch.pens).reverse().filter(e => (e !== '__jsogObjectId')).forEach(element => {
        var CommentsList = [];
        if (Batch.closed.comments[element]) {
          CommentsList = Batch.closed.comments[element];
          for(var i = 0; i < Object.keys(CommentsList).length; i++) {
            var name = Object.keys(CommentsList)[i]
            if (!name) continue;
            var object = CommentsList[name];
            if (!object || !object.time) continue;
            object.pen = element
            Comments.push({[name]:object})
          }
        }
        Comments_List = Comments_List.concat(CommentsList);
      });
    }
    Comments = Comments.sort((a, b) => (a.time > b.time) ? 1 : -1)
    return { isClosed, isVirtual, StartDate, EndDate, Animals, TargetWeight, AverageWeight, AverageGrowth, Comments_List, Comments }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    const CurrentData = this.props.firebase.currentData;

    if (CurrentData && CurrentData.batchId && localDataStore.batchs[CurrentData.batchId]){
      const batchData = localDataStore.batchs[CurrentData.batchId]
      
      this.props.getWeights(Object.keys(localDataStore.userData.farms)[0], localDataStore.weighings?localDataStore.weighings.update_time:null, localDataStore.sections)

      var PenMemTime = null
      if (localDataStore.pens) PenMemTime = localDataStore.pens.update_time
      Object.keys(batchData.pens).filter(e => e !== '__jsogObjectId').forEach(Pen => {
        this.props.getPen(Object.keys(localDataStore.userData.farms)[0], Pen, PenMemTime)
      })

      var TempGraph = ""
      if (CurrentData.curveId) {
        TempGraph = CurrentData.curveId;
      }
      this.props.getCurve(Object.keys(localDataStore.userData.farms)[0], TempGraph)
    }
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null) {
      NavigationService.navigate("AuthStack")
    }
    if (!props.firebase.currentData || !props.firebase.currentData.batchId){
      NavigationService.navigate("UserBatchs")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.localDataStore) {
        var { isClosed, isVirtual, StartDate, EndDate, Animals, TargetWeight, AverageWeight, AverageGrowth, Comments_List, Comments } = this.setData(this.props)
        this.setState({
          Prediction: false,
          isClosed: isClosed,
          isVirtual: isVirtual,

          StartDate: StartDate,
          EndDate: EndDate,
          Animals: Animals,
          TargetWeight: TargetWeight,
          AverageWeight: AverageWeight, 
          AverageGrowth:AverageGrowth,
          Comments_List: Comments_List,
          Comments: Comments,
        })
      }
    }
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid
    const CurrentData = this.props.firebase.currentData;

    var StartDate = this.state.StartDate

    var Dif_Time = new Date().getTime() - StartDate
    var Days = Dif_Time / (1000 * 3600 * 24);
    Days = Days.toFixed(0) //TODO: closed date...
    
    StartDate = new Date(StartDate).getDate() + "/" + (new Date(StartDate).getMonth() +1) + "/" + new Date(StartDate).getFullYear()

    var EndDate = this.state.EndDate
    EndDate = new Date(EndDate).getDate() + "/" + (new Date(EndDate).getMonth() +1) + "/" + new Date(EndDate).getFullYear()

    var Virtual = String(this.state.isVirtual)
    Virtual = Virtual.charAt(0).toUpperCase() + Virtual.slice(1)
    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]}>
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          <TitleElement Text={this.props.firebase.currentData.batchId?this.props.firebase.currentData.batchId:"-NON-"}/>
          <View>
            <View style={[Helpers.fullWidth, {justifyContent:'center'}]}>
              <FlatGrid
                itemDimension={120}
                spacing={10}

                data={[
                  {title: 'Start', data: StartDate},
                  {title: 'End Forecast', data: EndDate},
                  {title: 'Target Weight', data: this.state.TargetWeight},
                  //{title: 'Virtual cal', data: Virtual}
                ]}
                renderItem={({ item }) => (
                  <View style={[Helpers.borderContainer, {textAlign:'center'}]}>
                    <Text>{item.title}</Text>
                    <Text>{item.data}</Text>
                  </View>
                )}
                />
            </View>
            <View style={[Helpers.fullWidth, {justifyContent:'center'}]}>
              <FlatGrid
                itemDimension={120}
                spacing={10}

                data={[
                  {title: 'Animals', data: this.state.Animals},
                  {title: 'Day', data: Days},
                  {title: 'Avarage weight', data: this.state.AverageWeight},
                  {title: 'Average growth', data: this.state.AverageGrowth}
                ]}
                renderItem={({ item }) => (
                  <View style={[Helpers.borderContainer, {textAlign:'center'}]}>
                    <Text>{item.title}</Text>
                    <Text>{item.data}</Text>
                  </View>
                )}
                />
            </View>
            <Button Text="Show Predict"
                onPress={() => {
                  this.setState({
                    Prediction: !this.state.Prediction
                  })
                } }/>
          </View>
          <View style={[Helpers.fill, {minHeight:750}]}>
            <CurveElement Predict={this.state.Prediction} weighings={localDataStore.weighings}/>
          </View>
          {this.state.Comments_List?(
            <View style={[Helpers.fill, Helpers.midWidth, {height:415}]}>
              {/* diff for app / web here ^ */}
              <ContentProp texts={["Action log"]} datas={[]} />
              <View style={{height:10}} />
              <FlatList
                data={this.state.Comments.filter(e => (e !== '__jsogObjectId'))}
                renderItem={({item, index}) => {
                  var Comment = this.state.Comments_List[0][Object.keys(item)[0]]
                  if (!Comment) return null
                  return(
                    <ContentProp texts={[format_Time(Comment.time) + ":\n" + Comment.user]}
                                datas={[Comment.pen + ": " + Comment.action + ":\n" + Comment.data]}
                    />
                  )}
                }
                  //Setting the number of column
                  numColumns={1}
                  keyExtractor={(item, index) => String(index)}
                />
            </View>
          ):(<></>)}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    
    getPen: (farmId, penId, DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {FarmId: farmId, PenId: penId, DataTime:DataTime})),
    getWeights: (farmId, DataTime, MemSections) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.WEIGHING, {FarmId: farmId, DataTime: DataTime, MemSections: MemSections})),
    
    getBatch: (farmId, batchId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {FarmId: farmId})),
    getCurve: (farmid, curveid) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {FarmId: farmid, GraphId: curveid})),
    setCurve: (farmid, curve) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.GRAPH, {FarmId: farmid, Graph: curve}))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(DataDisplay);
//export default DataDisplay;